body {
  margin: 0;
  padding: 0;
  height: auto;
  background-image: url('https://img.freepik.com/free-vector/coffee-shop-instagram-post-background-vector_53876-136841.jpg');
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
}

/* Styling for the main container to ensure the content stays on top */
.home-container {
  background-color: rgba(200, 214, 175, 0.8); /* More distinct green */
  width: 80%;         /* 80% of the viewport width */
  height: auto;        /* 80% of the viewport height */
  margin: 20px auto;       /* Centers the container horizontally */
  display: flex;      /* Enables flexbox */
  flex-direction: column; /* Stacks children vertically */
  align-items: center;    /* Centers children horizontally */
  justify-content: flex-start; /* Centers children vertically */
  text-align: center;
  position: relative;
  padding: 5%;        /* Adds 5% padding inside the container */
  border: 2px solid #053225;
  border-radius: 10px; /* Optional: Add rounded corners */
  overflow: hidden;
}
.home-container h2 {
  font-family: 'Palatino', serif;
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #053225;
}

/* Quiz page container with a new shade of green */
.quiz-container {
  background-color: rgba(200, 214, 175, 0.8);  /* Adjusted green */
  background-size: cover;
  min-height: 100vh;
  height:auto;
  text-align: center;
  position: relative;
  
}

.custom-box {
  border-color: #44200D;
  border-top-style: solid;
  border-bottom-style: solid;
  border-left-style: dashed;
  border-right-style: dashed;
  border-radius: 10%;
  border-width: 5px;
  margin: 10px;
  width: fit-content;
}
/* Green button styling for both home and quiz */
.home-button {
  background-color: #053225;
  border-color: #053225;
  position: absolute;
  left: 30px;
  top: 30px;
  width: 150px;
  height: 50px;
  border-radius: 20px;
}

.home-button:focus, 
.home-button:focus-visible, 
.home-button:active {
  outline: none !important;
  box-shadow: none !important;
}
.home-button:hover{
  background-color: #C8D6AF;
}

.quiz-title {
  font-family: 'Palatino', serif;
  font-weight: bold;
}

.quiz-description {
  font-family: 'Palatino', serif;
}

.progress-container {
  width: 50%;
  margin: 0 auto;
}

.progress-bar {
  width: 100%;
  border: 3px solid #772e25;
  border-radius: 7px;
  background-color: #FFECCC;
  height: 30px;
  position: relative;
  overflow: hidden;
}

.progress-fill {
  background-color: #053225;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: #FFFFFF;
  transition: width 0.5s ease;
}

.question-container {
  position: relative;
  margin-top: 1%;
  border: 5px solid #772e25;
  width: 75%;
  height: 90%;
  padding-top: 3%;
  background-color: #FFEECC;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 5%;
}

.option-col {
  margin: 1%;
  margin-top: 8%;
  padding: 3%;
}

.option-button {
  width: 150px; /* Set a fixed width */
  height: 130px; /* Set a fixed height */
  border-radius: 20%;
  background-color: #053225;
  border-color: #053225;
  transition: none;
}
.option-button:hover{
  background-color: #C8D6AF;
  border-color: #C8D6AF;
}

.option-button.selected {
  background-color: #C8D6AF;
  border-color: #C8D6AF;
  color:#44200D;
  transition: none;
}

.option-button:focus, 
.option-button:focus-visible, 
.option-button:active {
  outline: none !important;
  box-shadow: none !important;
}


.navigation-buttons {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.results-button {
  display: block;
  margin: 20px auto 0; /* Centered below the question container */
  width: 15%;
  height: 50px;
  background-color: #053225;
  border-color: #053225;
  transition: background-color 0.3s ease;
}

.results-button:hover {
  background-color: #053225;
  border-color: #053225;
}

.results-button:active {
  background-color: #C8D6AF;
}

.prev-button,
.next-button {
  width: 25%;
  height: 50px;
  background-color: #053225 !important;
